@use 'base/variables' as v;
@use 'base/mixins' as m;

.galeria {
    padding: 5rem 0;
   

    h3 {
        text-align: center;
    }
}

.galeria-imagenes {
    @include m.resetear-lista;
    @include m.grid(2, 1rem);

    @include m.phone {
        @include m.grid(3, 1rem);
    }
    @include m.tablet {
        @include m.grid(4, 2rem);
    }
    @include m.desktop {
        @include m.grid(6, 2rem);
    }

}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;

    display: grid;
    place-items: center center;

    background-color: rgb(0 0 0 / 0.5);

    picture {
        display: block;
        max-width: 80rem;
        
    }
}

.btn-cerrar {
    background-color: v.$blanco;
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: v.$fuenteTitulo;
    font-size: 2.5rem;
    line-height: 1;
    cursor: pointer;
    
}