@use 'variables' as v;

@mixin phone {
    @media (min-width: v.$phone) {
        @content;
    }
};
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
};
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
};
@mixin desktopXL {
    @media (min-width: v.$desktopXL) {
        @content;
    }
};

@mixin grid($columnas, $gap) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $gap;
}

@mixin resetear-lista {
    margin: 0;
    padding: 0;
    list-style: none;
}


