@use 'base/variables' as v;
@use 'base/mixins' as m;

.line-up {
    background-color: v.$rosa;
    padding: 5rem 0;

    h3, p {
        text-align: center;
        color: v.$blanco;
    }
    .dia {
        text-transform: uppercase;
        margin-bottom: 5rem;
        margin-top: 3rem;
    }
}

.escenarios-contenedor {
    @include m.tablet {
        @include m.grid(2, 5rem);
    }
    p {
        color: v.$negro;
    }
}

.calendario {
    @include m.resetear-lista;

    li {
        padding: 2.5rem;
        font-weight: 700;
    }
    span {
        font-weight: 400;
    }
    
}

.bg-amarillo {
    background-color: v.$amarillo;
    li:nth-child(odd) {
        background-color: darken(v.$amarillo , 15%);
    }
}
.bg-verde {
    background-color: v.$verde;    
    li:nth-child(odd) {
        background-color: darken(v.$verde , 10%);
    } 
}
.shadow {
    box-shadow: 0px 0px 5px 5px rgba(10,10,10,0.2);
}

.escenario {
    padding-top: 2rem;
    margin-bottom: 2rem;
    .nombre-escenario {
        &:after {
            content: '';
            display: block;
            width: 20rem;
            height: 10rem;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
            margin: 2rem auto 0 auto;
        }
    }
}

.edm .nombre-escenario:after {
    background-image: url(../img/icono_edm.png);
}
.pop .nombre-escenario:after {
    background-image: url(../img/icono_rock.png);
}

